import React, { useState } from "react";
import "./Navigation.css";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";

const CartNav = () => {
  let showed = false;
  function showCart() {
    console.log(showed);
    let cartModal = document.getElementById("cart-modal");
    let wrapper = document.getElementById("main-wrapper");
    
    if (showed === false) {
      cartModal.style.transform = "translateY(0)";
      wrapper.style.opacity = "0.3";
      wrapper.style.pointerEvents = "none";
      document.getElementsByTagName("body")[0].style.overflowY = "hidden";
      showed = true;
    } else if (showed === true) {
      cartModal.style.transform = "translateY(101%)";
      wrapper.style.opacity = "1";
      wrapper.style.pointerEvents = "auto";
      document.getElementsByTagName("body")[0].style.overflowY = "scroll";
      showed = false;
    }
  }
  
  return (
    <div id="nav-mobile">
      <div class="page-inner">
        <div class="page-middle">
          <a href="/">
            <div class="nav2-left-top"></div>
          </a>
        </div>
      </div>
      <motion.img
        src={
          !showed
            ? "data:image/svg+xml;base64,"
            : "data:image/svg+xml;base64,"
        }
        drag
        layout
        dragConstraints={{ top: 0, bottom: 0, left: -400, right: 50 }}
        dragMomentum={false}
        dragTransition={{ bounceStiffness: 600, bounceDamping: 20 }}
        id="nav-mobile-bottom"
        onClick={showCart}
      />
      <div id="cart-modal">
        <div className="title">
          <p className="textHeader">
            {"Order "}
            <br />
          </p>
        </div>
        <button className="buyButton">Checkout</button>
      </div>
    </div>
  );
};

export default CartNav;
